// @flow
import React, { useEffect, useContext } from 'react';
import type { Element } from 'react';
import { Loader } from 'react-loaders';
import BlockUi from 'react-block-ui';
import { toastr } from 'react-redux-toastr';
import { useQuery } from '@apollo/client';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import _get from 'lodash/get';
import _flatMap from 'lodash/flatMap';
import _isEmpty from 'lodash/isEmpty';

import { withErrorBoundary } from 'components/ErrorBoundary';
import UserContext from 'contexts/UserContext';
import { GET_USER_OVERVIEW_OFFERS, GET_USER_OVERVIEW_PROPERTIES_OFFERS } from 'graphql/queries/user/overview';
import { OFFER_ERROR_MESSAGES } from 'shared/Messages';

import { USER_ROLES, getRole, displayContingencies } from './helper';

const useStyles = makeStyles(theme => ({
  loading: {
    border: '1px solid #E0E0E0',
    backgroundColor: theme.palette.common.white,
    minHeight: '100px',
  },
}));

const Contingencies = (): Element<Object> => {
  const classes = useStyles();
  const user = useContext(UserContext);
  const role = getRole(user);
  const isBuyer = role === USER_ROLES.BUYER;

  const { data: userOffers, loading: isLoading, error: offerError } =
    useQuery(isBuyer ? GET_USER_OVERVIEW_OFFERS : GET_USER_OVERVIEW_PROPERTIES_OFFERS,
      { errorPolicy: 'all', skip: _isEmpty(role) }
    );

  const offers = isBuyer ?
    _get(userOffers, ['getUserOverview', 'Offers', 'Items'], []) :
    _flatMap(
      _get(userOffers, ['getUserOverview', 'Properties', 'Items'], []),
      (sellerProperty) => sellerProperty.Offers
    );

  useEffect(() => {
    if (!role) return;

    const message = _get(offerError, ['message'], '');
    if (message) {
      toastr.error(OFFER_ERROR_MESSAGES.fetchFailure, message);
    }
  }, [role, offerError]);

  if (_isEmpty(offers) || !role) return <></>;

  return (
    <BlockUi
      tag="div"
      blocking={isLoading}
      loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
      className={clsx({
        [classes.loading]: isLoading,
      })}
    >
      {displayContingencies(offers, isBuyer)}
    </BlockUi>
  );
};

export default (withErrorBoundary(Contingencies): Element);
